import { useState } from 'react';

export default function Counter({ title }) {
  const [count, setCount] = useState(0);

  return (
    <div className="counter">
      <h2>{title}</h2>
      <div className="counter-display">{count}</div>
      <button onClick={() => setCount(c => c + 1)}></button>
      <button onClick={() => setCount(c => c - 1)}></button>
    </div>
  );
}